$(document).ready(function () {
  var wrestlerData = {};
  var wrestlerDataItemSelected = {};
  var wrestlerSlug = $('#wrestlerSlug').val();

  if (!wrestlerSlug)
    return;

  var currentStarsSelected = "1_1";
  var currentTierSelected = "Bronze";


  $.getJSON('/db/wrestler/' + wrestlerSlug + '.json', function(data){
    wrestlerData = data;
    updateData();
    resetUI();
  });

  function updateData() {
    wrestlerDataItemSelected = wrestlerData[currentTierSelected][currentStarsSelected];
  }

  function resetUI() {
    $('#wrestlerLevelUI').text(1 + "/" + wrestlerDataItemSelected.LevelCap);
    $('#wrestlerMaxLevel').val(wrestlerDataItemSelected.LevelCap);
    $('#wrestlerLevelInput').val(1);
    $('#wrestlerLevelInput').prop('min', 1);
    $('#wrestlerLevelInput').prop('max', wrestlerDataItemSelected.LevelCap);
    updateStatsUI(1);
    updateAbilitiesUI(wrestlerDataItemSelected.Abilities);
  }

  function updateStatsUI(level) {
    var stats = getStatsForLevel(level);
    $('#stat-hp').text(stats.HP);
    $('#stat-red').text(stats.Red);
    $('#stat-black').text(stats.Black);
    $('#stat-yellow').text(stats.Yellow);
    $('#stat-blue').text(stats.Blue);
    $('#stat-green').text(stats.Green);
    $('#stat-purpel').text(stats.Purpel);
  }

  function getAbilityUIRow(ability) {
    var uiRow = $('<div/>', {'class': 'row', 'sylte': 'margin-bottom:10px;'})
    .append(
      $('<div/>', {'class': 'col-xs-3'})
      .append(
        $('<img/>', {'src': '/img/Gems/' + ability.gemType + '.png', 'width': 45, 'height': 45, 'alt': ability.gemType + ' Gem', 'class': 'center-block'})
      )
      .append(
        $('<p/>', {'text': ability.cost, 'class': 'text-center'})
      )
    )
    .append(
      $('<div/>', {'class': 'col-xs-9'})
      .append(
        $('<div/>', {'class': 'row'})
        .append(
          $('<div/>', {'class': 'col-xs-12'})
          .append(
            $('<p/>')
            .append(
              $('<strong/>', {'text': ability.name})
            )
          )
        )
        .append(
          $('<div/>', {'class': 'col-xs-12'})
          .append(
            $('<p/>', {'text': ability.levels[0].text, 'class': 'ability-text'})
          )
        )
        .append(
          $('<div/>', {'class': 'col-xs-11'})
          .append(
            $('<div/>', {'class': 'level-selector'})
            .append(
              $('<p/>')
              .append(
                $('<span/>', {'text': 1, 'class': 'abilitySelectedLevel'})
              )
              .append(
                '/'
              )
              .append(
                $('<span/>', {'text': ability.levelCap, 'class': 'abilityMaxLevel'})
              )
            )
            .append(
              $('<input/>', {'type': 'range', 'name': 'ability-level', 'min': 1, 'max': ability.levelCap, 'value': 1}).on('input', function() {
                var abilityLevel = $(this).val();
                var $curLevelSpan = $(this).parent().find('.abilitySelectedLevel').first();
                var $curAbilityDescription = $(this).parent().parent().parent().find('.ability-text').first();
                var abilityID = $(this).next().val();
                var abilityItem = getAbilityForLevel(abilityID, abilityLevel);

                $curLevelSpan.text(abilityLevel);
                $curAbilityDescription.text(abilityItem.text);
              })
            )
            .append(
              $('<input/>', {'class': 'ability-id', 'name': 'ability-id', 'type': 'hidden', 'value': ability.ID})
            )
          )
        )
      )
    )

    return uiRow;
  }

  function updateAbilitiesUI(abilities) {
    $('#abilities').empty();
    abilities.forEach(function(ability, index, array){
      $('#abilities').append(getAbilityUIRow(ability));
    });
  }

  function getStatsForLevel(level) {
    for (var i in wrestlerDataItemSelected.Stats) {
      var thisWrestlerStats = wrestlerDataItemSelected.Stats[i];
      if (thisWrestlerStats.level == level) {
        return thisWrestlerStats.stats;
      }
    }
  }

  function getAbility(abilityID) {
    var theseAbilities = wrestlerDataItemSelected.Abilities;
    for (var i in theseAbilities) {
      if (theseAbilities[i].ID == abilityID) {
        return theseAbilities[i];
      }
    }
    return false;
  }

  function getAbilityForLevel(abilityID, abilityLevel) {
    var thisAbility = getAbility(abilityID);
    var thisAbilityLevels = thisAbility.levels;
    for (var i in thisAbilityLevels) {
      if (thisAbilityLevels[i].level == abilityLevel) {
        return thisAbilityLevels[i];
      }
    }
    return false;
  }

  $('#wrestlerLevelInput').on('input', function() {
    var curLevel = $(this).val();
    var maxLevel = $('#wrestlerMaxLevel').val();
    $('#wrestlerLevelUI').text(curLevel + "/" + maxLevel);
    updateStatsUI(curLevel);
  });

  $('input[name="stars"]').on('change', function() {
    currentStarsSelected = $(this).val();
    updateData();
    resetUI();
  });

  $('input[name="tier"]').on('change', function() {
    currentTierSelected = $(this).val();
    updateData();
    resetUI();
  });
});



